.statistics {
  width: 250px;
  border: 2px solid #ccc3c3;
  border-radius: 9px;
  margin: 30px auto;
  text-align: center;
  box-shadow: 0px 8px 30px rgba(212, 20, 67, 0.1);
}
.title {
  font-size: 22px;
  color: dimgray;
}
.list {
  list-style: none;
  display: flex;
  margin: 0px;
  width: 251px;
  padding: 0px;
}
.item {
  display: flex;
  justify-content: center;
  color: rgb(30, 72, 210);
}
.list_item {
  width: 100%;
  border: 1px solid grey;
  padding-bottom: 5px;
  padding-top: 5px;
}

